import { BrowserRouter, Routes, Route } from "react-router-dom";
import "./App.css";
import Home from "./screens/Home/Home";
import Login from "./screens/Login/Login";
import Registro from "./screens/Registro/Registro";
import Producto from "./screens/Producto/Producto";
import ResultadoBusqueda from "./screens/ResultadoBusqueda/ResultadoBusqueda"
import Reserva from "./screens/Reserva/Reserva";
import Confirmacion from "./screens/Confirmacion/Confirmacion";

function App() {
  return (
    <BrowserRouter>
      <div className="App">
        <Routes>
          <Route Route index element={<Home />} />
          <Route Route path="/login" element={<Login />} />
          <Route Route path="/registro" element={<Registro />} />
          <Route Route path="/resultadobusqueda" element={<ResultadoBusqueda />} />
          <Route Route path="/producto/:id" element={<Producto/>} />
          <Route Route path="/producto/:id/reserva" element={<Reserva />} />
          <Route Route path="/producto/:id/reserva/confirmacion" element={<Confirmacion />} />
        </Routes>
      </div>
    </BrowserRouter>
  );
}

export default App;
