import { useState, useEffect } from "react";
import { useSearchParams } from "react-router-dom";
import CardProducts from "../../components/Cards/CardProducts";
import { getProductsPorCiudad } from "../../server/server";

const ResultadoBusqueda = () => {
 
    const [searchParams] = useSearchParams()

    const [dataServer, setDataServer] = useState([]);

	useEffect(() => {
		getProductsPorCiudad(searchParams.get("id")).then(result => setDataServer(result));
	}, []);

  return (
    <>
      <h1>Hola marquitos hernan</h1>

      <ul className="product__list">
        {dataServer.map((p, idx) => {
          return (
            <li key={idx.toString()}>
              <CardProducts
                id={p.id}
                image={p.imagenes[0].url}
                category={p.categoria.titulo}
                title={p.nombre}
                location={p.ciudad.nombre}
                description={p.descripcion}
                calification="8"
                review="Muy bueno"
              />
            </li>
          );
        })}
      </ul>
    </>
  );
};

export default ResultadoBusqueda;
