import "./Home.css";
import Header from "../../layout/Header/Header";
import HeaderUser from "../../layout/Header/HeaderUser";
import Body from "../../layout/Body/Body";
import SearchBar from "../../components/SearchBar/SearchBar";
import CategoriesList from "../../components/Listados/CategoriesList";
import ProductsList from "../../components/Listados/ProductsList";
import Footer from "../../layout/Footer/Footer";

const Home = () => {
	  const credenciales = JSON.parse(window.localStorage.getItem("items"));
	return (
		<>
			{credenciales === null ? (
				<Header />
			) : (
				<HeaderUser
					nombre={credenciales.Cnombre}
					apellido={credenciales.Capellido}
				/>
			)}
			<Body>
				<SearchBar />
				<section className='sec__categories'>
					<h2>Buscar por tipo de alojamiento</h2>
					<div className='cat__container'>
						<CategoriesList />
					</div>
				</section>
				<section className='sec__products'>
					<h2>Recomendaciones</h2>
					<ProductsList />
				</section>
			</Body>
			<Footer />
		</>
	);
};

export default Home;
