import React, { useEffect, useState } from "react";
import { useParams } from "react-router";
import "./Producto.css";
import Header from "../../components/ComponentesProducto/Header/Header";
import Ubicacion from "../../components/ComponentesProducto/Ubicacion/Ubicacion";
import Fotos from "../../components/ComponentesProducto/SeccionFotos/Fotos";
import Detalle from "../../components/ComponentesProducto/Detalle/Detalle";
import CalendarioProd from "../../components/ComponentesProducto/CalendarioProd/CalendarioProd";
import Mapa from "../../components/ComponentesProducto/Mapa/Mapa";
import TenesQueSaber from "../../components/ComponentesProducto/TenesQueSaber/TenesQueSaber";
import Modal from "../../components/ComponentesProducto/Modal/Modal";
import { getProductsId } from "../../server/server";

function Producto() {
	const [dataServer, setDataServer] = useState(null);

	const { id } = useParams();

	useEffect(() => {
		getProductsId(id).then(result => setDataServer(result));
	}, [id]);

  return (
		<div className='Producto'>
			{dataServer && (
				<>
					<Header data={dataServer} />
					<Ubicacion data={dataServer} />
					<Fotos data={dataServer} />
					<Detalle data={dataServer} />
					<CalendarioProd id={dataServer.id} />
					<Mapa data={dataServer} />
					<TenesQueSaber data={dataServer} />
				</>
			)}
		</div>
  );
}

export default Producto;
