import React,{useState,useEffect} from "react";
import CardProducts from "../Cards/CardProducts";
import "./ProductsList.css";
import { getProducts } from "../../server/server";

const ProductList = () => {

	const [dataServer, setDataServer] = useState([]);

	useEffect(() => {
		getProducts().then(result => setDataServer(result));
	}, []);

	return (
		<>
			<ul className='product__list'>
				{dataServer.map((p,idx) => {
					return (
						<li key={idx.toString()}>
							<CardProducts
								id={p.id}
									image={p.imagenes[0].url}
									category={p.categoria.titulo}
									title={p.nombre}
									location={p.ciudad.nombre}
									description={p.descripcion}
									calification='8'
									review='Muy bueno'
								/>
						</li>
					);
				})}
			</ul>
		</>
	);
};

export default ProductList;
