import '../Registro/Registro.css';
// import '../../../App.css' // CSS General a toda la app
import Button from '../../components/Button/Button';
import React, {useState} from 'react';
import { Link, useNavigate } from "react-router-dom";
import HeaderLogin from '../../layout/Header/HeaderLogin';
import Footer from '../../layout/Footer/Footer';

function Login() {
  
  const navigate = useNavigate();
  const [data, setData] = useState({

    email: "",
    password: "",

})
  const handleChange = (e) => {
    setData({
        ...data,
        [e.target.name]: e.target.value
    })
}
  const handleSubmit = async (e) => {
    e.preventDefault();
    let credenciales = JSON.parse(localStorage.getItem('items'));
    console.log(data.email + "   "+ data.password)
    if((data.email === credenciales.Cemail)&&(data.password === credenciales.Cpassword)){
      navigate('/');
    }else alert ("Por favor vuelva a intentarlo, sus credenciales son inválidas")
    }

  return (
		// eslint-disable-next-line react/self-closing-comp
    <>
      <HeaderLogin />
			<div className='Login'>
				<h2 className='Heading-1'>Iniciar Sesión</h2>
				<form onSubmit={handleSubmit}>
					<div className='ContainerMail'>
						<label className='Text-2'>
							{" "}
							Correo Electrónico
							<input
								type='email'
								name='email'
								id='inputEmail'
								className='InputMail'
								onChange={handleChange}
								required
							/>
						</label>
					</div>
					<div className='ContainerMail'>
						<label className='Text-2'>
							{" "}
							Contraseña
							<input
								type='password'
								name='password'
								id='inputPassword'
								className='InputMail'
								onChange={handleChange}
								required
							/>
						</label>
					</div>

					<div className='ContainerMail'>
						<Button> Ingresar </Button>
					</div>
					<p className='Text-2'>
						¿Aún no tenés cuenta?{" "}
						<Link to='/registro'>Registrate</Link>
					</p>
				</form>
      </div>
      <Footer />
		</>
  );
}

export default Login;